body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgb(40, 44, 52);
  color: azure;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.content {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  height: 50vh;
  font-size: 2rem;
}


.header {
  background-color:rgb(29, 32, 37);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

button {
  font-size: 1.2rem;
  border-radius: 5px;
  background-color: cadetblue;
  margin: 3px;
  
}
button:hover {
  cursor: pointer;
  box-shadow: 2px 2px 2px rgb(27, 27, 27);
  background-color:rgba(21, 35, 63, 0.123);
  color: azure;
  transition: all 0.2s;
  
}

#button:active {
  cursor: pointer;
  box-shadow: 2px 2px 2px rgb(27, 27, 27);
  background-color:rgba(13, 21, 37, 0.123);
  color: azure;
  
}

.section {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

div {
  display:  flex;
}

.form {
  top: 100px
}